median {
  overflow: hidden;
  border-radius: 20px;
  display: inline-block;
  height: 40px;
}
median .startLabel {
  fill: #fff;
  stroke-width: 0;
}
median .startLabel text {
  fill: #606060;
}
median .endLabel {
  fill: none;
  stroke: #fff;
  stroke-width: 1px;
}
median .endLabel text {
  fill: #fff;
  stroke-width: 0;
}
median .axis {
  font: 10px sans-serif;
  -webkit-user-select: none;
  -moz-user-select: none;
  user-select: none;
}
median .axis .domain {
  fill: none;
  stroke: #000;
  stroke-opacity: 0.3;
  stroke-width: 10px;
  stroke-linecap: round;
}
median .axis .halo {
  fill: none;
  stroke: #ddd;
  stroke-width: 40px;
  stroke-linecap: round;
}
median .slider .handle {
  fill: #0ca;
  stroke-width: 0;
  cursor: ew-resize;
}
median .slider text {
  -webkit-user-select: none;
  -moz-user-select: none;
  user-select: none;
  cursor: ew-resize;
}
